import React, { useState } from "react";
import { Layout, Row, Col, Form, Input, Button, notification } from "antd";
import { LoggedinHeader } from "../../containers";
import "./form.scss";
import AccSidebar from "../manageOrg/AccSidebar";
import AmWebSidebar from "../hotlineMessages/AmWebSidebar";
import OrgSidebar from "../../containers/OrgSidebar";
import FloatLabel from "./FloatLabels";
import axios from "axios";
import * as AppStorage from "../../utils/AppStorage";
import authHeader from "../../services/auth-header";
import { BASE_URL } from "../../userconfig";
const { Content } = Layout;

const ChangePassword = () => {
  const [api, contextHolder] = notification.useNotification();
  const userType = AppStorage.get("userType");
  const [pwd, setPwd] = useState({
    currentPwd: "",
    newPwd: "",
    confirmPwd: "",
  });

  const handleOnChange = (e) => {
    setPwd({
      ...pwd,
      [e.target.id]: e.target.value,
    });
  };

  const handleSubmit = () => {
    const token = authHeader();
    const data = {
      old_password: pwd.currentPwd,
      new_password: pwd.newPwd,
      confirm_password: pwd.confirmPwd,
    };
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    axios.patch(BASE_URL + "changepassword/", data, options).then((res) => {
      const placement = "bottomLeft";
      api.info({
        description: "Your Password has been changed successfully.",
        placement,
      });
    });
  };
  
  return (
    <React.Fragment>
      <React.Fragment>
        {" "}
        {contextHolder}
        <Row className="inner-header">
          {userType === "AM WEB" ? (
            <LoggedinHeader amWEBUser />
          ) : userType === "AM CC" ? (
            <LoggedinHeader amCCUser />
          ) : userType === "Org CC" ? (
            <LoggedinHeader amORGUser />
          ) : null}
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              {userType === "AM WEB" ? (
                <AmWebSidebar />
              ) : userType === "Org CC" ? (
                <OrgSidebar />
              ) : userType === "AM CC" ? (
                <AccSidebar />
              ) : null}
              <Content className="">
                <Row className="py-3">
                  <h1 className="mb-0">Change Password</h1>
                  {/* <div className="d-block w-100 pl-1">
                    <small>
                      Your password was last modified on{" "}
                      <span className="font-weight-bold">22/04/2020</span>{" "}
                    </small>
                  </div> */}
                </Row>
                <div className="mid-content mt-4">
                  <Row>
                    <Col span={24}>
                      <Form className="input-md floating-label w-100">
                        <Col span={24}>
                          <Row
                            gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}
                            className="d-block"
                          >
                            <Col span={8} className="gutter-row">
                              <FloatLabel
                                label="Current Password"
                                value={pwd.currentPwd}
                              >
                                <Form.Item
                                  name="currentpassword"
                                  className="pwd required"
                                  rules={[
                                    {
                                      required: true,
                                      message:
                                        "Please enter your current password!",
                                    },
                                  ]}
                                >
                                  <Input.Password
                                    htmlFor="currentpassword"
                                    id="currentPwd"
                                    value={pwd.currentPwd}
                                    onChange={handleOnChange}
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={8} className="gutter-row">
                              <FloatLabel
                                label="New Password"
                                value={pwd.newPwd}
                              >
                                <Form.Item
                                  name="newpassword"
                                  className="pwd required"
                                  rules={[
                                    {
                                      required: true,
                                      message:
                                        "Please enter your new password!",
                                    },
                                  ]}
                                >
                                  <Input.Password
                                    htmlFor="newpassword"
                                    id="newPwd"
                                    value={pwd.newPwd}
                                    onChange={handleOnChange}
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={8} className="gutter-row">
                              <FloatLabel
                                label="Confirm Password"
                                value={pwd.confirmPwd}
                              >
                                <Form.Item
                                  name="confirmpassword"
                                  className="pwd confirpwd required"
                                  rules={[
                                    {
                                      required: true,
                                      message: "Please confirm your password!",
                                    },
                                  ]}
                                >
                                  <Input.Password
                                    htmlFor="confirmpassword"
                                    id="confirmPwd"
                                    value={pwd.confirmPwd}
                                    onChange={handleOnChange}
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                          </Row>
                        </Col>

                        <Form.Item>
                          <div className="mt-5">
                            <Button
                              type="primary"
                              htmlType="submit"
                              onClick={handleSubmit}
                            >
                              Submit
                            </Button>
                          </div>
                        </Form.Item>
                      </Form>
                    </Col>
                  </Row>
                </div>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </React.Fragment>
    </React.Fragment>
  );
};

export default ChangePassword;
