import React from "react";
import { Layout, Row, Col, Input, Select } from "antd";
import { LoggedinHeader } from "../../containers";
import "../manageEmp/manageEmp.scss";
import Grid from "../table/Table";
import Buttons from "../button/Button";
import { AudioOutlined } from "@ant-design/icons";
import { Link } from "react-router-dom";
import OpenlinePermissioModal from "./../modal/OpenlinePermissioModal";
import OrgSidebar from "../../containers/OrgSidebar";
import moment from "moment";
import OpenlinePopover from "../table/OpenlinePopover";

const { Search } = Input;

const suffix = (
  <AudioOutlined
    style={{
      fontSize: 16,
      color: "#1890ff",
    }}
  />
);
const { Content } = Layout;

class ShowAllOpnlines extends React.Component {
  constructor(props) {
    super(props);
    const columns = [
      { title: "Openline Name", dataIndex: "name", key: "name", width: "22%",
      render: (name) => {
        return {
          children: name,
          props: {
            'data-title': 'Openline Name',
          },
        };
      }
     },
      {
        title: "Start Date",
        dataIndex: "start_date",
        key: "start_date",
        width: "22%",
        render: (start_date) => {
          return {
            children: start_date,
            props: {
              'data-title': 'Start Date',
            },
          };
        }
      },
      {
        title: "End Date",
        dataIndex: "end_date",
        key: "end_date",
        width: "21%",
        render: (end_date) => {
          return {
            children: end_date,
            props: {
              'data-title': 'End Date',
            },
          };
        }
      },
      {
        title: "Permissions",
        dataIndex: "",
        key: "x",
        width: "20%",
        render: (selectedRows) => {
          console.log("selectedRows",selectedRows)
          return{
          children:
           <div>
             <OpenlinePermissioModal selectedRows={selectedRows} />
           </div>,
           props: {
            'data-title': 'Permissions',
           }
        }
      }
      },
      {
        title: "Status",
        dataIndex: "",
        key: "x",
        width: "10%",
        render: (record) =>{
          return{
          children:
          record.status_id === 5 ? (
            <div className="primary-tag-rounded tag">
              <span className="circle mr-1"></span>Active
            </div>
          ) : (
            <div className="secondary-tag-rounded tag">Inactive</div>
          ),
          props: {
            'data-title': 'Status',
           }
          }
        }
      },
      {
        title: "",
        dataIndex: "",
        key: "x",
        width: "5%",
        render: (record) =>
        {
          return{
            children:<OpenlinePopover data={record} />,
            props:{
              'data-title': 'Action',
            }
          }

        }
          
        },
    ];
    this.state = {
      columns: columns,
      data: [],
    };
  }
  
  componentDidMount() {
    let { openlines } = this.props;
    openlines.forEach((openline) => {
      openline.start_date = moment(openline.start_date).utc().local().format("MM / DD / YYYY")
      // abc.start_date = abc.start_date.replaceAll("T00:00:00Z", "");
      openline.end_date = moment(openline.end_date).utc().local().format("MM / DD / YYYY")
     // abc.end_date = abc.end_date.replaceAll("T00:00:00Z", "");
    });
    this.setState({ data: openlines }, () => {
      this.onSearch("");
    });
  }
  filterIt(arr, searchKey) {
    if (!searchKey || searchKey === "" || searchKey === null) {
      return arr;
    }
    return arr.filter(function (obj) {
      return Object.keys(obj).some(function (key) {
        if (typeof obj[key] === "string") {
          return obj[key].toUpperCase().includes(searchKey.toUpperCase());
        }
      });
    });
  }

  onSearch = (value) => {
    const { data } = this.state;
    const filterDt = this.filterIt(data, value);
    this.setState({ filterData: filterDt });
  };
  render() {
    return (
      <>
        <Row className="inner-header">
        <LoggedinHeader amORGUser/>
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
            <OrgSidebar amORGUser/>
              <Content className="">
                <Row className="py-3">
                  <h1>Openline Directory</h1>
                </Row>
                <Col span={24}>
                  <Row className="pt-1 pb-3 px-3 px-lg-0 sm-direction-reverse">
                    <Col span={24} lg={18} className="search-row mt-2 mt-lg-0">
                      <Row>
                        <Col span={24} lg={9} className="search-box">
                          <Search
                            placeholder="Search"
                            onSearch={this.onSearch}
                          />
                        </Col>
                      </Row>
                    </Col>

                    <Col
                       span={24} lg={6}
                       className="d-flex align-items-center justify-content-end"
                    >
                      <Link to="/orgcc/createopenline">
                        <Buttons value="Create Openline" />
                      </Link>
                    </Col>
                  </Row>
                </Col>
                <Grid
                  rowKey="hotline_id"
                  columns={this.state.columns}
                  data={this.state.filterData}
                />
              </Content>
            </Layout>
          </Layout>
        </Row>
      </>
    );
  }
}


export default ShowAllOpnlines;
