import React, { useEffect } from "react";
import OpenlineDirectory from "./OpenlineDirectory";
import ShowAllOpnlines from "./ShowAllOpnlines";
import { connect } from "react-redux";
import { getOpelineAsync } from "../../actions/openline";
import { getHotlinesAsync } from "../../actions/hotlines";


const OpenlineContainer = (props) => {
  useEffect(() => {
    const { dispatch } = props;
    dispatch(getOpelineAsync());
  }, []);

  return props.openlinesdata.length > 0 ? (
    <ShowAllOpnlines openlines={props.openlinesdata} />
  ) : (
    <OpenlineDirectory />
  );
};

function mapStateToProps(state) {
  const { openlinesdata } = state.openline;
  return {
    openlinesdata,
  };
}

export default connect(mapStateToProps)(OpenlineContainer);