import React, { useEffect, useState } from "react";
import {
    Form,
    Select,
    Input,
    Button,
    DatePicker,
    Alert,
    Row,
    Layout,
    Upload
} from "antd";
import * as AppStorage from "../../utils/AppStorage";

import moment from "moment";
import authHeader from "../../services/auth-header";
import axios from "axios";
import { CheckCircleTwoTone, UploadOutlined } from "@ant-design/icons";
import FloatLabel from "../form/FloatLabels";
import { LoggedinHeader } from "../../containers";
import OrgSidebar from "../../containers/OrgSidebar";
import { useHistory, useLocation } from "react-router-dom";
import "../form/form.scss";
import { BASE_URL } from "../../userconfig";

const { TextArea } = Input;
const { Option } = Select;
const { Content } = Layout;
const EditOpenline = ({ props }) => {
const [openLineData, setOpenLineData] = useState({
        end_date: "",
        help_text: "",
        openline_id: "",
        name: "",
        start_date: "",
        openline_image: "",
        status_id: "",
        iconHtml: ""
});

    const [error, showError] = useState(false);
    const history = useHistory();
    const [disabledbtn, setdisabledbtn] = useState(true);
    const [form] = Form.useForm();
    const [openlineErrorUpdated, setopenlineErrorUpdated] = useState("");
    let location = useLocation();

    useEffect(() => {
        const sDate = moment(location.state.end_date).format("YYYY-MM-DD");
        const eDate = moment(location.state.start_date).format("YYYY-MM-DD");
        let dates = [moment(sDate, "YYYY/MM/DD"), moment(eDate, "YYYY/MM/DD")];
        console.log(location);
        setOpenLineData({
            end_date: "",
            help_text: location.state.help_text,
            openline_id: location.state.openline_id,
            name: location.state.name,
            start_date: dates,
            status_id: location.state.status_id.toString(),
        });
    }, []);

    const handleStepOneOnChange = (e) => {
        setOpenLineData({ ...openLineData, [e.target.id]: e.target.value });
    };

    const handleStepOneDatePickerChange = (date, name) => {
        setOpenLineData({ ...openLineData, [name]: date });
    };

    const handleStepOneOnSelect = (value, name) => {
        setOpenLineData({ ...openLineData, [name]: value });
    };

    const editOpenline = (e) => {
        e.preventDefault();
        const user = JSON.parse(AppStorage.get("user"));
        const data = {
            end_date: moment(openLineData.start_date[1]).format("DD-MM-YYYY"),
            help_text: openLineData.help_text,
            openline_id: openLineData.openline_id,
            openline_image: openLineData.openline_image[0].base64,
            name: openLineData.name,
            start_date: moment(openLineData.start_date[0]).format("DD-MM-YYYY"),
            status_id: parseInt(openLineData.status_id),
            updated_by: user.user_details.org_user_id,
            created_by: user.user_details.org_user_id,

        };
        updateOpenLine(data);
    };

    const cancelHotline = (e) => {
        window.location = "/orgcc/openlinedirectory";
    }

    const updateOpenLine = (data) => {
        const token = authHeader();
        let openLineId = openLineData.openline_id;
        delete openLineData.hotline_id;
        const options = {
            headers: {
                Authorization: token,
                Accept: "application/json, text/plain",
                "Content-Type": "application/json;charset=UTF-8",
            },
        };
        axios.put(BASE_URL + "openline/" + openLineId, data, options).then((res) => {
            if (res.status === 200) {
                window.location = "/orgcc/openlinedirectory";
            }
        });
    };

    const handleStepOneUpload = (value,name ) =>{ 
        let fileList=value.fileList.slice(-1);
         fileList.forEach(function (file, index) {
           let reader = new FileReader();
           reader.onload = (e) => {
             file.base64 = e.target.result;
           };
           reader.readAsDataURL(file.originFileObj);
         });
         setOpenLineData({...openLineData,[name]:fileList})
       
     }

    const getIconText = (id) => {
        switch (id) {
            case "1":
                return "Sexual Harassment";
            case "2":
                return "Violence";
            case "3":
                return "Racism";
            case "4":
                return "Cyber Bullying";
            case "5":
                return "Fight";
            case "6":
                return "Discrimination";
            case "7":
                return "Slap";
            default:
                return "";
        }
    };

    const disabledDate = (current) => {
        return current && current < moment().startOf("day");
    };

    const CheckOpenlineName = (e) => {
        const token = authHeader();
        const options = {
            headers: {
                Authorization: token,
                Accept: "application/json, text/plain",
                "Content-Type": "application/json;charset=UTF-8",
            },
        };
if (e.target.value !== location.state.name) {
        axios
            .post(BASE_URL + "checkOpenline/", { openline_name: e.target.value }, options)
            .then((res) => {
                if (res.data) {
                    setopenlineErrorUpdated(res.data[0]);
                }
            });
   } else {
      setopenlineErrorUpdated(false);
    }
    };

    const { RangePicker } = DatePicker;

    return (
        <>
            <Row className="inner-header">
                <LoggedinHeader amORGUser />
            </Row>
            <Row className="content-center pt-0" data-test="manageEmp">
                <Layout>
                    <Layout>
                        <OrgSidebar amORGUser />
                        <Content className="">
                            <Row className="py-3">
                                <h1>Edit Openline</h1>
                            </Row>
                            <div className="mid-content mt-4">
                                <Form
                                    data-test="createHotlineStepOne"
                                    className="input-md w-md-100 w-75 floating-label"
                                    name="validate_other"
                                    form={form}
                                >
                                    {error && (
                                        <Form.Item>
                                            <Alert
                                                message="Please fill the fields marked with asterisk(*)"
                                                type="error"
                                                showIcon
                                            ></Alert>
                                        </Form.Item>
                                    )}
                                <FloatLabel label="Directory Name" value={openLineData.name}>
                                    <Form.Item
                                        className="required"
                                        name="directoryName"
                                        rules={[
                                            {
                                                required: true,
                                                message: "Please enter directory name",
                                            },
                                        ]}
                                    >
                                        <TextArea
                                            showCount
                                            maxLength={80}
                                            value={openLineData.name}
                                            onChange={(e) => handleStepOneOnChange(e)}
                                            onBlur={(e) => CheckOpenlineName(e)}
                                            id={"name"}
                                            className="input-control textarea-input"
                                            placeholder="Directory Name"
                                            htmlFor="directoryName"
                                        />
                                        {openlineErrorUpdated == "status: false" && (
                                            <CheckCircleTwoTone
                                                className="successCheck"
                                                twoToneColor="#52c41a"
                                            />
                                        )}
                                      
                                    </Form.Item>
                                    </FloatLabel>
                                    <FloatLabel
                                label="Description"
                                value={openLineData.help_text}
                                      >
                                    <Form.Item
                                        className="required"
                                        name="description"
                                        rules={[
                                            {
                                                required: true,
                                                message: "Please enter description",
                                            },
                                        ]}
                                    >
                                        <>
                                            <TextArea
                                                className="input-control textarea-input"
                                                value={openLineData.help_text}
                                                onChange={(e) => handleStepOneOnChange(e)}
                                                id={"help_text"}
                                                placeholder="Description"
                                                htmlFor="description"
                                            />
                                        </>
                                    </Form.Item>
                                    </FloatLabel>
                                    <FloatLabel label="Date" value="start_date">

                                    <Form.Item className="input-control datepicker-wrapper">
                                        <RangePicker
                                            className="datepicker w-100 w-lg-50 mr-0 mr-lg-5 required"
                                            name="start_date"
                                            value={openLineData.start_date}
                                            disabledDate={disabledDate}
                                            onChange={(date) =>
                                                handleStepOneDatePickerChange(date, "start_date")
                                            }

                                            rules={[
                                                {
                                                    required: true,
                                                    message: "Please select start date",
                                                },
                                            ]}
                                        />
                                    </Form.Item>
                                    </FloatLabel>
                                        <FloatLabel label="Status" value={openLineData.status_id}>
                                    <Form.Item
                                        className="required"
                                        name="status"
                                        rules={[
                                            {
                                                required: true,
                                                message: "Please select status",
                                            },
                                        ]}
                                    >
                                        <>
                                            <Select
                                                placeholder="Status"
                                                className="input-control w-100 w-lg-50"
                                                value={openLineData.status_id}
                                                htmlFor="status"
                                                onSelect={(value) =>
                                                    handleStepOneOnSelect(value, "status_id")
                                                }
                                            >
                                                
                                                <Select.Option value="5">Active</Select.Option>
                                                <Select.Option value="4">Inactive</Select.Option>
                                            </Select>
                                        </>
                                    </Form.Item>
                                      </FloatLabel>
                                    <Form.Item className="">
                                        <Form.Item
                                            className="upload-control"
                                            name="upload"
                                            valuePropName="fileList"
                                        >
                                            <Upload
                                                action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                                                listType="picture"
                                                fileList={openLineData.openline_image}
                                                onChange={(value) => handleStepOneUpload(value, "openline_image")}

                                            >
                                                <Button icon={<UploadOutlined />}>Upload Image</Button>
                                            </Upload>

                                            {getIconText(openLineData.openline_id) && (
                                                <div className="ant-upload-list ant-upload-list-picture">
                                                    <div className="ant-upload-list-item ant-upload-list-item-list-type-picture">
                                                        <div className="ant-upload-list-item-info d-inline-flex w-100">
                                                            <span className="d-inline-flex align-items-center">
                                                                <a className="ant-upload-list-item-thumbnail">
                                                                    {ReactHtmlParser(openLineData.iconHtml)}
                                                                </a>

                                                                <span
                                                                    className="ant-upload-list-item-name ant-upload-list-item-name-icon-count-1"
                                                                    title={getIconText(openLineData.openline_id)}
                                                                >
                                                                    {getIconText(openLineData.openline_id)}
                                                                </span>
                                                            </span>
                                                        </div>
                                                    </div>
                                                </div>
                                            )}
                                        </Form.Item>
                                    </Form.Item>
                                    <div className="steps-action mt-5">
                                        <Button
                                            type="primary"
                                            htmlType="submit"
                                            onClick={editOpenline}
                                            data-test="stepOneNext"
                                            style={{ margin: "0 8px" }}
                                        >
                                        Update
                                         </Button>
                                        <Button
                                            type="primary"
                                            htmlType="submit"
                                            onClick={cancelHotline}
                                            data-test="stepOneNext"
                                        >
                                            Cancel
                                        </Button>
                                    </div>

                                </Form>
                            </div>
                        </Content>
                    </Layout>
                </Layout>
            </Row>
        </>
    );
};
export default EditOpenline;